body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
}

#game-board {
  touch-action: manipulation;
  margin: 25px;
  padding: 5px;
  display: inline-block;
}

#game-board > div {
  margin: 3px;
}

.minefield {
  display: inline-block;
  box-shadow: 0 0 2px gray;
}

.game-cell {
  -webkit-user-select: none;
  user-select: none;
  text-align: center;
  color: #000;
  background-color: #fff;
  flex-direction: column;
  place-content: center;
  width: 30px;
  height: 30px;
  margin: 0;
  font-weight: bold;
  display: flex;
  box-shadow: inset 0 0 2px gray;
}

.game-cell.n1 {
  color: #00f;
}

.game-cell.n2 {
  color: green;
}

.game-cell.n3 {
  color: red;
}

.game-cell.n4 {
  color: #00008b;
}

.game-cell.n5 {
  color: #8b0000;
}

.game-cell.n6 {
  color: teal;
}

.game-cell.n7, .game-cell.n8 {
  color: #000;
}

.game-cell.un-revealed {
  color: #fff;
  background: #00008b;
}

.game-cell.unexploded-mine {
  background-color: #d3d3d3;
}

.game-cell.bad-flag {
  background-color: #ff0;
}

.game-cell.exploded {
  background-color: red;
}

.cell-row {
  margin: 0;
  padding: 0;
  display: flex;
}

.status-bar {
  display: block;
}

.status-bar > div {
  width: 50%;
  display: inline-block;
}

.game-timer {
  text-align: right;
}

